<template>
	<view class="contain">
		<view class="fixed">
			<cu-custom :isBack="true" :Color="Color" :backColor="backColor" :isIcon="true" bgColor="bg-shadeTop text-white">
				<block slot="backText"></block>
				<block slot="content">详情</block>
			</cu-custom>
		</view>
		<view class="wrapper">
			<view class="top">
				<u-parse v-if="obj1.content1 != 'undefined'" :content="obj1.content1" @navigate="navigate"></u-parse>
				<!-- <image class="bg" mode="widthFix" :src="v" v-for="(v,k) in obj1.images" :key="k" v-if="obj1.images.length > 0"></image> -->
				<view class="play" v-if="obj1.video_url">
					<video :src="obj1.video_url" objectFit="cover"></video>
				</view>
			</view>
			<view class="top" style="margin-top: 24rpx;padding-bottom: 24rpx;">
				<view class="comment">评论 {{obj1.commentNumber}}</view>
				<mescroll-uni :down="downOption" @down="downCallback" :up="upOption" @up="upCallback" @init="mescrollInit"
				@emptyclick="emptyClick" @topclick="topClick">
					<view class="item" v-for="(v,k) in list" :key="k">
						<image class="icon" :src="v.icon"></image>
						<view class="user">
							<view class="name">
								<view class="name-say">
									<view>{{v.nickname}}</view>
									<view style="color: #08A7C1;" @tap="goResponse(v.id)" v-if="!v.content1">回复</view>
								</view>
								<view style="font-size: 24rpx;color: #666;margin-top: 30rpx;">{{v.content}}</view>
							</view>
							<view class="other">
								<view class="response" v-if="v.content1">{{v.nickname1}}：{{v.content1}}</view>
								<view style="margin-top: 24rpx;">{{v.create_date}}</view>
							</view>
						</view>
					</view>
				</mescroll-uni>
			</view>
		</view>
		<view class="send">
			<input type="text" v-model="content" placeholder="爱评论的人，一定很有趣">
			<image src="../../static/images/fabu10.png" @tap="speak"></image>
		</view>
		<prompt :isMutipleLine="false" :visible.sync="promptVisible" inputStyle="" title="回复" placeholder="请输入回复内容"
		mainColor="#08A7C1" @confirm="clickPromptConfirm">
		</prompt>
	</view>
</template>

<script>
	import Toast from '../../common/toast.js'
	import {ajax} from '../../request/request.js'
	import MescrollUni from "../../components/mescroll-uni/mescroll-uni.vue"
	import Prompt from '../../components/zz-prompt/index.vue'
	import uParse from "../../components/feng-parse/parse.vue"
	export default {
		data() {
			return {
				Color: '#333',
				backColor: '#fff',
				content: '',
				promptVisible: false,
				list: [],
				mescroll: null, //mescroll实例对象
				downOption: {
					use: true, // 是否启用下拉刷新; 默认true
					auto: true, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
					autoShowLoading: true, // 如果设置auto=true(在初始化完毕之后自动执行下拉刷新的回调),那么是否显示下拉刷新的进度; 默认false
					isLock: false, // 是否锁定下拉刷新,默认false;
					isBoth: true, // 下拉刷新时,如果滑动到列表底部是否可以同时触发上拉加载;默认true,两者可同时触发;
					offset: 80, // 在列表顶部,下拉大于80upx,松手即可触发下拉刷新的回调
					inOffsetRate: 1, // 在列表顶部,下拉的距离小于offset时,改变下拉区域高度比例;值小于1且越接近0,高度变化越小,表现为越往下越难拉
					outOffsetRate: 0.2, // 在列表顶部,下拉的距离大于offset时,改变下拉区域高度比例;值小于1且越接近0,高度变化越小,表现为越往下越难拉
					bottomOffset: 20, // 当手指touchmove位置在距离body底部20upx范围内的时候结束上拉刷新,避免Webview嵌套导致touchend事件不执行
					minAngle: 45, // 向下滑动最少偏移的角度,取值区间  [0,90];默认45度,即向下滑动的角度大于45度则触发下拉;而小于45度,将不触发下拉,避免与左右滑动的轮播等组件冲突;
					textInOffset: '下拉刷新', // 下拉的距离在offset范围内的提示文本
					textOutOffset: '释放更新', // 下拉的距离大于offset范围的提示文本
					textLoading: '加载中 ...' // 加载中的提示文本
				},
				upOption: {
					use: true, // 是否启用上拉加载; 默认true
					auto: true, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
					isLock: false, // 是否锁定上拉加载,默认false;
					isBoth: true, // 上拉加载时,如果滑动到列表顶部是否可以同时触发下拉刷新;默认true,两者可同时触发;
					page: {
						num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
						size: 10, // 每页数据的数量
						time: null // 加载第一页数据服务器返回的时间; 防止用户翻页时,后台新增了数据从而导致下一页数据重复;
					},
					noMoreSize: 6, // 如果列表已无数据,可设置列表的总数量要大于等于5条才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
					textLoading: '加载中 ...', // 加载中的提示文本
					textNoMore: '-- 已经到底了 --', // 已经到底了的提示文本
					toTop: {
						// 回到顶部按钮,需配置src才显示
						src: "http://www.mescroll.com/img/mescroll-totop.png", // 图片路径
						offset: 1000, // 列表滚动多少距离才显示回到顶部按钮,默认1000
						duration: 300 // 回到顶部的动画时长,默认300ms
					},
					empty: {
						use: true, // 是否显示空布局
						icon: "http://www.mescroll.com/img/mescroll-empty.png", // 图标路径
						tip: '还没有发布评论', // 提示
						// btnText: '去逛逛 >' // 按钮
					}
				},
				obj: {},
				obj1: {},
				ids: ''
			}
		},
		components: {
			MescrollUni,
			Prompt,
			uParse
		},
		onUnload() {
			this.mescroll = null;
		},
		//注册滚动到底部的事件,用于上拉加载
		onReachBottom() {
			this.mescroll && this.mescroll.onReachBottom()
		},
		//注册列表滚动事件,用于下拉刷新
		onPageScroll(e) {
			this.mescroll && this.mescroll.onPageScroll(e)
		},
		onLoad(option) {
			this.obj.id = option.id
		},
		methods: {
			// mescroll组件初始化的回调,可获取到mescroll对象
			mescrollInit(mescroll) {
				this.mescroll = mescroll
			},
			/*下拉刷新的回调 */
			downCallback(mescroll) {
				// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
				// loadSwiper();
				// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
				this.list = []
				this.mescroll.resetUpScroll()
			},
			/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
			upCallback(mescroll) {
				let pageNum = mescroll.num // 页码, 默认从1开始
				let pageSize = mescroll.size // 页长, 默认每页10条
				let self = this
				self.init(mescroll)
			},
			// 点击回到顶部按钮的回调
			topClick() {
				console.log('点击了回到顶部按钮');
			},
			init(mescroll) {
				//获取搜索数据
				let self = this
				let data1 = {
					cmd: 'getHearthById',
					id: this.obj.id,
					uid: uni.getStorageSync('uid'),
					nowPage: mescroll.num
				}
				Toast.showLoading('', function() {
					let data = {
						data: data1,
						success: function(res) {
							console.log(res)
							self.obj1 = res.data
							if (res.data.result == 0) {
								Toast.hideLoading()
								if (res.data.dataList) {
									for (var j = 0; j < res.data.dataList.length; j++) {
										self.list.push(res.data.dataList[j])
									}
								}
							} else {
								Toast.hideLoading()
								Toast.showToast(res.data.resultNote)
							}
							// 接口返回的当前页数据列表 (数组)
							let curPageData = []
							if (res.data.dataList) {
								curPageData = res.data.dataList
							}
							// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
							let totalPage = 1
							if (res.data.totalPage) {
								totalPage = res.data.totalPage
							}
							mescroll.endByPage(curPageData.length, totalPage)
						}
					}
					ajax(data)
				})
			},
			speak() {
				let self = this
				if(this.content == '') {
					Toast.showToast('评论内容不能为空')
					return
				}
				let data1 = {
					cmd: 'commentHerath',
					id: this.obj.id,
					uid: uni.getStorageSync('uid'),
					content: this.content
				}
				Toast.showLoading('', function() {
					let data = {
						data: data1,
						success: function(res) {
							if (res.data.result == 0) {
								Toast.hideLoading()
								self.content = ''
								Toast.showToast('评论成功')
								self.list = []
								self.mescroll.resetUpScroll()
							} else {
								Toast.hideLoading()
								Toast.showToast(res.data.resultNote)
							}
						}
					}
					ajax(data)
				})
			},
			clickPromptConfirm(val) {
				if(val == '') {
					Toast.showToast('回复内容不能为空')
					return
				}
				let self = this
				let data1 = {
					cmd: 'replyComment',
					id: this.ids,
					content: val,
					uid: uni.getStorageSync('uid')
				}
				Toast.showLoading('', function() {
					let data = {
						data: data1,
						success: function(res) {
							if (res.data.result == 0) {
								Toast.hideLoading()
								Toast.showToast('回复成功')
								self.promptVisible = false
								self.list = []
								self.mescroll.resetUpScroll()
							} else {
								Toast.hideLoading()
								Toast.showToast(res.data.resultNote)
							}
						}
					}
					ajax(data)
				})
			},
			goResponse(id) {
				this.promptVisible = true
				this.ids = id
			}
		}
	}
</script>

<style lang="scss" scoped>
	.contain {
		width: 100%;
		min-height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		background: #f6f6f6;

		.fixed {
			width: 100%;
		}

		.send {
			width: 100%;
			height: 90rpx;
			background: #fff;
			display: flex;
			padding: 0 28rpx;
			box-sizing: border-box;
			justify-content: space-between;
			align-items: center;
			position: fixed;
			left: 0;
			bottom: 0;
			z-index: 99999;

			input {
				width: 85%;
				height: 70rpx;
				padding: 0 24rpx;
				box-sizing: border-box;
				font-size: 28rpx;
				color: #333;
				background: #F2F2F2;
				border-radius: 30px;
			}

			image {
				width: 50rpx;
				height: 50rpx;
			}
		}

		.wrapper {
			width: 100%;
			padding: 28rpx 28rpx 120rpx;
			box-sizing: border-box;
			display: flex;
			flex-direction: column;

			.top {
				width: 100%;
				padding: 20rpx;
				box-sizing: border-box;
				background: #fff;
				border-radius: 4px;
				display: flex;
				flex-direction: column;

				.content {
					width: 100%;
					font-size: 28rpx;
					color: #333;
					padding-bottom: 24rpx;
					border-bottom: 1px solid #eee;
					text-align: justify;
				}
				
				.bg {
					width: 100%;
					height: 360rpx;
					border-radius: 4px;
					margin-bottom: 32rpx;
				}

				video {
					width: 100%;
					height: 360rpx;
				}
				
				.comment {
					width: 100%;
					height: 60rpx;
					line-height: 60rpx;
					font-size: 28rpx;
					color: #333;
					position: relative;
					padding-left: 20rpx;
				}

				.comment:before {
					content: '';
					position: absolute;
					width: 4px;
					height: 30rpx;
					border-radius: 20px;
					background: #08A7C1;
					top: 15rpx;
					left: 0;
				}

				.item {
					width: 100%;
					display: flex;
					margin-top: 40rpx;

					.icon {
						width: 100rpx;
						height: 100rpx;
						border-radius: 50%;
						margin: 0;
					}

					.user {
						width: 75%;
						display: flex;
						flex-direction: column;

						.name {
							width: 100%;
							display: flex;
							flex-direction: column;
							justify-content: space-around;
							font-size: 28rpx;
							color: #333;
							padding-left: 24rpx;
							box-sizing: border-box;
							
							.name-say {
								width: 100%;
								display: flex;
								justify-content: space-between;
								align-items: center;
								font-size: 28rpx;
								color: #333;
							}
						}

						.other {
							width: 100%;
							margin-left: 24rpx;
							box-sizing: border-box;
							display: flex;
							flex-direction: column;
							font-size: 28rpx;
							color: #666;
							padding-bottom: 20rpx;
							border-bottom: 1px solid #eee;
							

							.response {
								width: 100%;
								padding: 24rpx;
								box-sizing: border-box;
								background: #F6F6F6;
								border-radius: 4px;
								margin: 24rpx 0 0;
							}
						}
					}
				}
			}
		}
	}
</style>
